.gallery .g-card{
    margin: 12px 0px 12px 0px;
    position: relative;
    border-radius: 10px;
    min-height: 250px;
    background-color: antiquewhite;
    overflow: hidden;
}
.gallery .g-card .g-image img{
    border-radius: 10px 10px 0 0;
    height: 200px;
    width: 100%;
    background-color: antiquewhite;
}
.gallery .g-card .g-image img:hover{
    transform: scale(1.1);
    opacity: 0.8;
}
.gallery .g-card .g-content p{
    margin: 10px;
    text-align: center;
}
.gallery .g-card .g-label{
    position: absolute;
    top: 10px;
    right: -1px;
    color: white;
    background-color: #eb8b10;
    padding: 1px;
    margin: 1px;
}

.gallery .green{
    background-color: green !important;
}
.gallery .blue{
    background-color: blue !important;
}